import React, { useEffect, useState } from 'react';
import './about.css';
import Video from "../assets/3d_video.mp4"
import Category from "./category";
import { Link } from "gatsby";

const imageSrc =[
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg"
]
const linkSrc = [
  "/civil_architecture",
  "/mining_heavy-industry",
  "/automotive"
]
const About = () => {
  const [moreClicked, setClicked] = useState("");
  const [imgPath, setImgPath] = useState("");
  const [linkPath, setlinkPath] = useState("");

  const handleCategory = () => {
    if (moreClicked !== 'active') {
      setClicked("active");
    } else {
      setClicked("");
    }
  }
  let index = 0;
  let timeHandle;
  useEffect(() => {
    timeHandle = setInterval(() => {
      if(index === imageSrc.length) {
        index=0;
        setImgPath(imageSrc[index]);
        setlinkPath(linkSrc[index]);
        index++;
      }
      else {
        setImgPath(imageSrc[index]);
        setlinkPath(linkSrc[index]);
        index++;
      }
    }, 4000)
    return () => clearInterval(timeHandle);
  }, [])

  return (
    <div id="about">
      <div className="aboutContainer">
        <div className="flex1 flexHeight">
          <div className="fontFour textBox">
            <h2>Our Projects</h2>
            <p>ManSysCo has been engaged, and repeatedly, successfully delivered a number of projects to our expanding base of clients.</p>
            <button onClick={handleCategory}>
              Learn more <i className="fas fa-angle-double-right"></i>
            </button>
          </div>
        </div>
        <div className= "flex2 garelly flexHeight">
          <Link to={linkPath} >
            <img 
              alt="Projects example images"
              src={imgPath}
              width= "100%"
              height= "100%"
            />
          </Link>
          <Category
            divClass={"categories flexItem " + moreClicked}
          />
        </div>
      </div>
      
      <div className="aboutContainer">
        <div className="flex3 flexHeight">
            <video autoPlay loop preload="auto">
              <source src={Video} type="video/mp4"></source>
              <track default/>
            </video>
        </div>
        <div className="flex4 flexHeight">
          <div className="fontFour textBox">
            <h2>Our Mission</h2>
            <p>Our mission is your wish</p>
            <Link to="/">
              <i className="fas fa-angle-double-left"></i> Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About;